@import url(https://fonts.googleapis.com/css2?family=Open+Sans+Condensed:wght@700&family=Open+Sans:wght@300;400;600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Oswald:wght@300;500;700&amp;display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face {
  font-family: 'MyArial';
  src: URL(/certificate-generator-front/static/media/arial.bc353358.ttf) format('truetype');
  font-weight: 400;
  font-style: normal;
  font-display: block;
}

.modal {
  padding-right: 30%;
}
.modal-footer{
  align-items: baseline;
  border-top: none;
}
.modal-content {
  flex-direction: unset;
}
.my-bg-primary {
  background-color: #40739e;
}

.bold {
  font-weight: 700;
}
.main{
  position:fixed;
  width: 100%;
  height: 75%;
  padding-right: 10%;
  padding-left: 10%;
}

.divider{
	position: relative;
	margin-top: 50px;
  margin-bottom: 50px;
	height: 1px;
}

.div-transparent:before{
	content: "";
	position: absolute;
	top: 0;
	width: 100%;
	height: 1px;
	background-color: #000000;
}

.div-dot:after{
	content: "";
	position: absolute;
	z-index: 1;
	top: -9px;
	left: calc(50% - 9px);
	width: 18px;
	height: 18px;
	background-color: #C4C4C4;
	border-radius: 50%;
	box-shadow: inset 0 0 0 2px white,
					0 0 0 4px white;
}

.upload-btn-wrapper {
  position: relative;
  overflow: hidden;
  display: inline-block;
}

.upload-btn-wrapper input[type=file] {
  font-size: 100px;
  position: absolute;
  left: 0;
  top: 0;
  opacity: 0;
}

.upload-btn-wrapper button:disabled, button[disabled] .btn{
  border: 1px solid #999999;
  background-color: #cccccc;
  color: #666666;
}

.btn{
  font: normal 20px/1 Oswald, sans-serif;
  color: #ffffff;
  background:    #64a4bd;
  border-radius: 11px;
  margin-left: 10px;
  padding:       20px 45px;
  display:       inline-block;
  text-align:    center;
}

.link-instructions{
  text-decoration: none;
  font-size: 1.0rem;
  color: #000000;

  border: 1px solid #F2F2F2;
  text-shadow: 0px 4px 4px #000000;
}


.box-fileupload {
    background: #d3e7ff;
    width: 100%;
    height: 100%;
    padding: 50px;
    border: 3px dashed #8fd9ea;
    border-radius: 6px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-flow: column;
}
.file-upload-input {
    display: none;
}
label.file-upload-btn {
    width: 182px;
    height: 120px;
    background-image: url(http://www.pngall.com/wp-content/uploads/2/Upload-PNG-Image-File.png);
    background-size: cover;
    background-position: center center;
    background-origin: border-box;
    background-repeat: no-repeat;
}
.file-upload-wrapper-title {
    width: 92%;
    min-height: 50px;
    padding: 10px;
    box-sizing: border-box;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-flow: column;
}
.file-upload-wrapper-title h4 {
    display: inline-block;
    font-size: 20px;
    padding: 10px 0px 6px 0;
    font-weight: 200;
}
.file-upload-wrapper-title hr {
    width: 21%;
    display: inline-block;
    margin: 6px 0;
}
.file-upload-wrapper-title__btn {
    background-color: #4099ff;
    border-color: #4099ff;
    border: none;
    padding: 12px 20px;
    color: #fff;
    cursor: pointer;
    transition: all ease-in 0.3s;
    border-radius: 2px;
}
.box-fileupload__label {
    font-size: 20px;
    margin: 10px 0;
    color: #1471ad;
}
.image-preview {
    display: block;
    width: 100%;
}
.image-preview__delete-btn {
    width: 25px;
    height: 25px;
    background: #000000ab;
    color: #fff;
    border-radius: 28px;
    font-size: 15px;
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 5px;
    left: 15px;
    cursor: pointer;
    z-index: 2;
}
.image-preview__delete-btn::before,
.image-preview__delete-btn::after {
    content: "";
    display: block;
    width: 56%;
    height: 2px;
    background: #fff;
    position: absolute;
}
.image-preview__delete-btn::after {
    transform: rotate(43deg);
}
.image-preview__delete-btn::before {
    transform: rotate(-40deg);
}

:root {
  --progress: 0%;
}

.progress-button {
  position: absolute;
  top: 100%;
  left: 72%;
  bottom: 100%;
  transform: translate(-50%, -50%);
  text-align: center;
}

.download-button {
  position: relative;
  width: 150px;
  border-radius: 25px;
  border: 0;
  padding: 10px 40px;
  font-family: "Lobster";
  font-size: 21px;
  box-sizing: content-box;
  cursor: pointer;
  overflow: hidden;
  transition: width 0.9s cubic-bezier(0.4, 0, 0.2, 1);
  box-shadow: 0px 5px 10px rgba(33, 33, 33, 0.25),
    inset 0 5px 10px rgba(33, 33, 33, 0.25);
}

download-button:focus {
  outline: none;
}

.download-button:after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  width: 0%;
  width: var(--progress);
  background-image: linear-gradient(
    -45deg,
    #252535 25%,
    #252535 25%,
    #252535 50%,
    #252535 50%,
    #252535 75%,
    #252535 75%,
    #252535
  );
  background-size: 50px 50px;
  border-radius: 25px;
  transition: all 0.6s cubic-bezier(0.4, 0, 0.2, 1);
}

.button-text {
  display: inline-block;
  opacity: 1;
  color: #252535;
  transition: all 0.6s cubic-bezier(0.4, 0, 0.2, 1);
}

.loading-text,
.percentage {
  display: block;
  color: #252535;
  font-size: 25px;
  position: absolute;
  top: 50%;
  left: 50%;
  /* transform: translate(-50%, -50%); */
  z-index: -1;
  opacity: 0;
  transition: all 0.6s cubic-bezier(0.4, 0, 0.2, 1);
}

.in-progress .loading-text {
  opacity: 1;
  top: -55%;
  transform: translate(-50%, -100%);
}

.in-progress .percentage {
  opacity: 1;
  top: 155%;
  transform: translate(-50%, 160%)
}

.in-progress .button-text {
  transform: translateY(-35px);
  opacity: 0;
}

.in-progress .download-button {
  width: 300px;
}

.finished .download-button {
  background: #252535;
}

.finished .download-button:after {
  opacity: 0;
}

